import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import back from '@/components/back'
export default {
	components: {
		swiper,
		swiperSlide,
		back
	},
	data() {
		return {
			swiperOption: {
				slidesPerView: 'auto',
				spaceBetween: 15,
				centeredSlides: true,
				loop: true,
				autoplay: {
					delay: 2000,
					disableOnInteraction: true
				},
			},
			id: this.$route.query.id,
			info: null,
			title:""
		};
	},
	created() {
		this.pull()
	},
	mounted() {
		document.title = this.$route.meta.title
	},
	methods: {
		pull() {
			this.$get("/MDTAPI/MDT/QuerySpecialistHosGroupMDT", {
				SpecialistHosGroupId: this.id
			}).then(o => {
				this.info = o
				this.title = this.info.HosGroupName
				/*this.info.ImgPaths = [
					"http://resource.newstarthealth.cn//ad/image/20190428/6369206810765208236314404.png",
					"http://resource.newstarthealth.cn//ad/image/20190428/6369206810765208236314404.png",
					"http://resource.newstarthealth.cn//ad/image/20190428/6369206810765208236314404.png"
				]*/
			})
		}
	}
};



